<template>
  <div class="admin-reset-password-form">
    <AuthTitle>
      {{ tp('resetPassword') }}
    </AuthTitle>
    <Form class="admin-reset-password-form__main d-flex flex-column align-items-center">
      <RadioButtons
        v-model="resetVia"
        :data="resetViaOptions"
        class="admin-reset-password-form__main__radio"
      >
        <template #default="{ item }">
          {{ item.title }}
        </template>
      </RadioButtons>
      <Text
        v-if="resetVia?.key"
        mb="8px"
        mt="40px"
        class="admin-reset-password-form__main__label"
      >
        {{ tp(resetVia?.key + 'ToResetLabel') }}
      </Text>
      <Input
        v-if="resetVia?.key === 'email'"
        v-model="form.email.value"
        hide-shadow
        bordered
        :placeholder="t('social.email')"
        class="admin-reset-password-form__main__input"
      >
        <template #prepend>
          <Icon opacity="0.4" name="mail" />
        </template>
      </Input>
      <Input
        v-else
        v-model="form.uid.value"
        hide-shadow
        bordered
        :placeholder="'UID'"
        class="admin-reset-password-form__main__input"
      />

      <Text
        mt="8px"
        mb="20px"
        :style="{
          opacity: error ? 1 : 0
        }"
        class="admin-reset-password-form__main__error"
      >
        {{ tp('resetField') }}
      </Text>

      <div class="admin-reset-password-form__main__actions">
        <Button type="primary" @click="onSubmit">
          <Text
            weight="400"
            size="14px"
            line-height="16px"
            color="white"
          >{{ tp('resetPassword') }}</Text>
        </Button>
        <Button @click="onBack">
          <Text
            weight="400"
            size="14px"
            line-height="16px"
            color="black"
          >{{ t('adminAuth.login.backBtn') }}</Text>
        </Button>
      </div>
    </Form>
  </div>
</template>

<script>
import Form from '@/shared/components/Form/Form'
import Text from '@/shared/components/UIKit/Text/Text'
import Button from '@/shared/components/UIKit/Button'
import AuthTitle from '@/admin/views/Auth/AuthTitle'
import { useI18n } from '@/shared/plugins/i18n'
import { useGoBack } from '@/shared/plugins/vue-router'
import { ROUTES_NAMES } from '@/admin/common/routes'
import { computed, onMounted, reactive, ref } from 'vue'
import RadioButtons from '@/shared/components/UIKit/RadioButtons'
import Input from '@/shared/components/UIKit/Input'
import Icon from '@/shared/components/Icon/Icon'

export default {
  name: 'ResetPasswordForm',
  components: {
    Icon,
    Input,
    RadioButtons,
    Button,
    Text,
    Form,
    AuthTitle
  },
  emits: ['submit'],
  setup (_, { emit }) {
    const { t, tp } = useI18n('adminAuth.resetPassword')
    const goBack = useGoBack({
      name: ROUTES_NAMES.DASHBOARD
    })

    const resetVia = ref()
    const error = ref(false)
    const form = reactive({
      email: {
        value: '',
        error: false,
        validate: () => {
          form.email.error = !form.email.value.length
          return !form.email.error
        }
      },
      uid: {
        value: '',
        error: false,
        validate: () => {
          form.uid.error = !form.uid.value.length
          return !form.uid.error
        }
      }
    })

    const resetViaOptions = computed(() => [
      { key: 'email', title: tp('resetViaEmail') },
      { key: 'uid', title: tp('resetViaUid') }
    ])

    onMounted(() => {
      resetVia.value = resetViaOptions.value[0]
    })

    const onSubmit = () => {
      error.value = validate()
      if (error.value) {
        return
      }
      emit('submit', form)
    }
    const onBack = () => {
      goBack()
    }
    const validate = () => {
      const key = resetVia.value.key
      form[key].validate()

      return form[key].error
    }

    return {
      t,
      tp,
      form,
      error,
      resetVia,
      resetViaOptions,
      onBack,
      onSubmit
    }
  }
}
</script>

