      width: 100%;

      .radio-buttons__item_selected,
      .radio-buttons__item_selected:last-child {
        background-color: #FBF3EC;
        border-color: map-get($colors, "primary") !important;
      }
    }
    &__label {
      width: 100%;
    }
    &__input {
      min-height: 44px;
      width: 100%;
    }
    &__error {
      color: $adminErrorColor;
    }
    &__actions {
      display: flex;
      width: 100%;

      .button {
        padding: 14px 35px;

        &:first-of-type {
          flex-grow: 1;
          margin-right: 20px;
        }
      }
    }
  }
}
</style>
